.footer-outer-container{
    background-color: #111;
    color: gray;
    display: flex;
    justify-content: center;
}
.footer-inner-container{
    width: 60%;
    /* background-color: red; */

}
.footer-icons{
    /* background-color: blue; */
    width: 12%;
    display: flex;
    justify-content: space-between;
    /* margin-left: 10%; */
    color: #fff;
    padding: 0;
}
.footer-data{
    display: flex;
    justify-content: space-between;
}
.footer-data ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 12%;
    /* background-color: blue; */

}
.footer-data ul li{
    cursor: pointer;
}
.service-code{
display: flex;
align-items: center;
justify-content: center;
border: #fff 1px solid;
width: 10%;
margin: 10px 0;
cursor: pointer;
}
.copy-write{
    font-size: 12px;
    margin-bottom: 10px;
}
